<style global>
  @font-face { 
    font-family: 'GenevaRegular';
    src: url('GenevaRegular.ttf') format('truetype'); 
  }

  /*** RESET ***/

  :global(html), :global(body), :global(div), :global(span), :global(applet), :global(object), :global(iframe), :global(h1), :global(h2), :global(h3), :global(h4), :global(h5), :global(h6), :global(p), :global(blockquote), :global(pre), :global(a), :global(abbr), :global(acronym), :global(address), :global(big), :global(cite), :global(code), :global(del), :global(dfn), :global(em), :global(img), :global(ins), :global(kbd), :global(q), :global(s), :global(samp), :global(small), :global(strike), :global(strong), :global(sub), :global(sup), :global(tt), :global(var), :global(b), :global(u), :global(i), :global(center), :global(dl), :global(dt), :global(dd), :global(menu), :global(ol), :global(ul), :global(li), :global(fieldset), :global(form), :global(label), :global(legend), :global(table), :global(caption), :global(tbody), :global(tfoot), :global(thead), :global(tr), :global(th), :global(td), :global(article), :global(aside), :global(canvas), :global(details), :global(embed), :global(figure), :global(figcaption), :global(footer), :global(header), :global(hgroup), :global(main), :global(menu), :global(nav), :global(output), :global(ruby), :global(section), :global(summary), :global(time), :global(mark), :global(audio), :global(video) {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
  }

  /*** GLOBAL ***/

  :global(html), :global(body), :global(main) {
    position: relative;
    width: 100%;
    height: 100%;
  }

  :global(body) {
    box-sizing: border-box;
    font-family: GenevaRegular, Geneva, sans-serif;
    font-size: 24px;
    line-height: 32px;
    background-color: var(--color-beige);
    color: var(--color-red);
  }

  @media (max-width: 500px) {
    :global(body) {
      font-size: 18px;
      line-height: 25px;
    }
  }

  :global(a) {
    color: var(--color-red);
  }

  :global(i) {
    font-style: italic;
  }

  :global(h2), :global(h3), :global(p) {
    margin-bottom: 1.4em;
  }

  :global(h2), :global(h3) {
    text-align: center;
    font-size: 28px;
    line-height: 34px;
  }

  @media (max-width: 500px) {
    :global(h2) {
      font-size: 22px;
      line-height: 28px;		
    }
  }

  :global(:root) {
    --color-mint: #00FF94;
    --color-beige: #F0E8C6;
    --color-red: #FF3A52;
  }
</style>